}
.groupSilver{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 20%;
}
.silverCover{
  width: 100%;
  height: 100%;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 0 5px;
}
.silverBtn{
  width: 95%;
